"use client";
import React, { useState } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";

// ۱. وارد کردن کامپوننت و تایپ از سیستم طراحی
import { Sidebar, type SidebarNavItem } from "@my-ds/design-system";

// آیکون‌ها
import { FaCog, FaShieldAlt, FaStore, FaListAlt,  } from "react-icons/fa";

// ۲. داده‌های منو (مختص اپلیکیشن)
const menuItems: SidebarNavItem[] = [
    {
        label: "فروشگاه‌ها",
        icon: FaStore,
        submenu: [
          { href: "/pages/Shops", label: "لیست فروشگاه‌ها", icon: FaListAlt },
          // ... بقیه آیتم‌های فروشگاه
        ],
    },
    // ... بقیه آیتم‌های منو
];

const footerItems: SidebarNavItem[] = [
    { href: "/settings", label: "تنظیمات", icon: FaCog }
];

// کامپوننت اصلی و هوشمند Sidebar
const AppSidebar = ({ isSidebarOpenForMobile, onCloseMobile }: any) => {
  const pathname = usePathname();
  const [isPinned, setIsPinned] = useState(false);
  const [isHovering, setIsHovering] = useState(false);

  const isDesktopExpanded = isPinned || isHovering;
  const shouldBeFullyOpen = isSidebarOpenForMobile || isDesktopExpanded;

  // ۳. تعریف تابع renderLink برای Next.js
  const renderNextLink = (href: string, children: React.ReactNode) => {
    return <Link href={href} legacyBehavior passHref><a>{children}</a></Link>;
  };

  // شما می‌توانید منطق wrapper و دکمه پین را اینجا نگه دارید
  // یا آن‌ها را هم به design-system منتقل کنید.
  return (
    <div 
        onMouseEnter={() => window.innerWidth > 768 && setIsHovering(true)}
        onMouseLeave={() => window.innerWidth > 768 && setIsHovering(false)}
    >
      {/* می‌توانید دکمه پین را روی این wrapper قرار دهید */}
      <Sidebar
        appName="پنل مدیریت"
        logo={<FaShieldAlt />}
        menuItems={menuItems}
        footerItems={footerItems}
        currentPath={pathname}
        isExpanded={shouldBeFullyOpen}
        renderLink={renderNextLink}
      />
    </div>
  );
};

export default AppSidebar;